<template>
    <div class="AdminPer">
        <div class="AdminTop">
            <i>当前位置：首页</i> >
            <span>人员管理</span>
        </div>
        <div class="content">
        <div class="AdminContent clearfix">
            <div class="search clearfix">
                <div class="searchOne">
                    <el-select v-model="region" placeholder="请选择" @change="changeReg(region)">
                        <el-option
                            v-for="item in regions"
                            :key="item"
                            :label="item"
                            :value="item">
                        </el-option>
                    </el-select>
                </div>
                <div class="searchTwo">
                    <el-select v-model="dept" placeholder="请选择" @change="changeDept(dept)">
                        <el-option
                            v-for="item in departs"
                            :key="item"
                            :label="item"
                            :value="item">
                        </el-option>
                    </el-select>
                </div>
            </div>
            <div class="ipt">
                <input type="text" placeholder="请输入查询的关键字" v-model="content" @keyup.enter.native="search(1)">
                <button @click="search(1)"><i class="el-icon-search"></i>查询</button>
            </div>
            <div class="btn">
                <el-upload
                        style="width: 130px;float: left;"
                        action="/api/user-import"
                        :on-remove="handleRemove"
                        multiple
                        :limit="1"
                        :on-exceed="handleExceed"
                        accept="application/xlsx"
                        auto-upload
                        >
                    <!--:file-list="fileList"-->
                    <!--<el-button size="small" type="primary">点击上传</el-button>-->
                    <button ><i class="el-icon-edit-outline"></i>Excel导入</button>
                </el-upload>
                <!--<input id="upload" type="file" @change="importfxx(this)"  accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" />-->
                <!--<button><i class="el-icon-edit-outline"></i>Excel导入</button>-->
                <button  @click="show">添加人员</button>
            </div>

            <!--添加人员弹框-->
            <div class="model" v-show="isShow">
                <div class="box">
                    <div class="boxHeader">
                        <span>添加人员</span>
                        <i class="el-icon-close" @click="boxHide"></i>
                    </div>
                    <div class="boxIpt">
                        <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="100px">
                            <el-form-item label="地区" prop="addregion"><b>*</b>
                                <el-select v-model="ruleForm.addregion" placeholder="请选择地区" @change="getAdd(ruleForm.addregion)">
                                    <el-option
                                        v-for="item in addRegions"
                                        :key="item"
                                        :label="item"
                                        :value="item">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="部门" prop="adddept"><b>*</b>
                                <el-select v-model="ruleForm.adddept" placeholder="请选择部门" @change="getAdd()">
                                    <el-option v-for="item in addDepts"
                                        :key="item.id"
                                        :label="item.section_two"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="姓名" prop="name"><b>*</b>
                                <el-input v-model="ruleForm.name"></el-input>
                            </el-form-item>
                            <el-form-item label="密码" prop="pass"><b>*</b>
                                <el-input type="password" v-model="ruleForm.pass" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="确认密码" prop="checkPass"><b>*</b>
                                <el-input type="password" v-model="ruleForm.checkPass" auto-complete="off"></el-input>
                            </el-form-item>
                        </el-form>
                    </div>
                    <div class="modelBtn">
                        <button @click="submitForm('ruleForm')">保存</button>
                    </div>
                </div>
            </div>
        </div>

        <!--人员显示-->
        <div class="listTable" v-loading="loading">
            <table>
                <tr>
                    <td>地区</td>
                    <td>部门</td>
                    <td>姓名</td>
                    <td>管理</td>
                </tr>
                <tr v-for="(item,index) in lists" :key="index">
                    <td>{{item.section_one}}</td>
                    <td>{{item.section_two}}</td>
                    <td>{{ item.name }}</td>
                    <td v-for="i in opt">
                        <span><router-link :to="'/superView/' + item.id">{{i.view}}</router-link></span><span>|</span>
                        <span><router-link :to="'/superEdit/' + item.id">{{i.edit}}</router-link></span><span>|</span>
                        <span @click="deltPer(item.id)">{{i.del}}</span>
                    </td>
                </tr>
            </table>
        </div>
        <p v-if="myShow" style="font-size: 16px;text-align: center;margin-top:40px;">暂无人员</p>
        <!--分页-->
        <div class="page">
            <div class="block">
                <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-size="10"
                    background
                    layout="total,prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </div>
        </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    //   表单验证
      var validatePass = (rule, value, callback) => {
          if (value === "") {
              callback(new Error("请输入密码"));
          } else {
              if (this.ruleForm.checkPass !== "") {
                  this.$refs.ruleForm.validateField("checkPass");
              }
              callback();
          }
      };
      var validatePass2 = (rule, value, callback) => {
          if (value === "") {
              callback(new Error("请再次输入密码"));
          } else if (value !== this.ruleForm.pass) {
              callback(new Error("两次输入密码不一致!"));
          } else {
              callback();
          }
      };
      return {
          ruleForm: {// 添加人员表单
              addregion: "",//地区
              adddept: "",//部门
              name: "",//姓名
              pass: "",//密码
              checkPass: "",//确认密码
          },
          rules2: {//添加人员表单验证
              pass: [{ required: true, validator: validatePass, trigger: "blur" }],
              checkPass: [
                  { required: true, validator: validatePass2, trigger: "blur" }
              ],
              addregion: [{ required: true, message: "请选择地区", trigger: "blur" }],
              adddept: [{ required: true, message: "请选择部门", trigger: "blur" }],
              name: [{ required: true, message: "请输入姓名", trigger: "blur" }]
          },
          myShow: false, //是否有显示的数据
          isShow: false,//弹框是否显示
          currentPage: 1,//当前页码
          regions: ['全部'],//地区选择
          region: "地区",//地区默认选项
          departs: ['全部'],//部门选择
          dept: "部门",//部门默认选项
          departss: ['全部'],//部门选择
          depts: "部门",//部门默认选项
          content:'',//查询输入的关键词
          addRegions:[],//添加地区选择
          addDepts:[],//添加部门选择
          lists: [], //人员信息
          opt:[{view: "查看", edit: "编辑", del: "删除"}],//查看，编辑，删除操作
          total:0,//人员总数
          loading:false,
//	      fileList:[],//上传的文件列表
      };
  },

  methods: {
//      获取添加地区部门信息
      getAdd(val) {
          let reg = [];
          this.$axios.post("/api/section-list", {
              section_one: val
          }).then((res)=> {
//              console.log(res)
              if (val) {
                  this.addDepts = res.data.data.data;
              } else {
                  for (var i in res.data.data.data) {
                      reg.push(res.data.data.data[i].section_one);
                  }
                  this.addRegions = this.unique(reg);
              }
          }).catch((error)=> {
              console.log(error);
            });
      },

//      搜索查询
      search(val){
          this.loading=true
          let dep=['全部'];//部门暂存
          this.departs=['全部'];
          this.lists=[];
          let paramy = {"number":10,"page":val};
          if (this.region&&this.region!=="地区"&&this.region!=="全部") {
              this.$set(paramy, 'section_one', this.region);
          };
          if(this.dept&&this.dept!=="部门"&&this.dept!=="全部"){
              this.$set(paramy, 'section_two', this.dept);
          };
          if(this.content){
              this.$set(paramy, 'name', this.content);
          };
          this.$axios.post('/api/section',paramy).then((res)=> {
//              console.log(res)
              if(res.data.data!=null){//数据不为空
                  this.lists=res.data.data.data;
                  this.total = res.data.data.total;
                  this.myShow = false;
              }else{//暂无数据，显示暂无此人员
                  this.myShow = true;
                  this.total=0;
              }

              //查询完成之后重新获取全部部门
              if(this.region&&this.region!=="地区"&&this.region!=="全部"){//地区选择的不是'全部'
                  this.$axios.post('/api/section-list',{
                    "section_one":this.region
                  }).then((res)=> {
//                      console.log(res)
                      for(var i in res.data.data.data){
                          dep.push(res.data.data.data[i].section_two);
                      }
                      this.departs = this.unique(dep);
                  }).catch((error)=> {
                      console.log(error)
                  })
              }else if(this.region=="全部"||this.region=="地区"){//地区选择的是'全部'或者不选择的时候是默认为地区
                  this.$axios.post('/api/section-list').then((res)=> {
//                      console.log(res)
                      for(var i in res.data.data.data){
                          dep.push(res.data.data.data[i].section_two);
                      }
                      this.departs = this.unique(dep);
                  }).catch((error)=> {
                      console.log(error)
                  })
              }

              this.loading=false;
          }).catch((error)=> {
              console.log(error);
          })
      },

//        选择改变地区
      changeReg(val) {
          let dep = ["全部"];
          this.departs = [];
          this.dept='';
          let paramy = {};
          if (val!=='全部') {
            this.$set(paramy, 'section_one', val);
          }
          this.$axios.post("/api/section-list", paramy).then((res)=> {
//            console.log(res)
              for (var i in res.data.data.data) {
                  dep.push(res.data.data.data[i].section_two);
              }
              this.departs = this.unique(dep);
              this.search(this.currentPage);
          }).catch((error)=> {
              console.log(error);
          });
      },

//        选择改变部门
      changeDept(val){
          let dep = ["全部"];
          this.departss = [];
          this.depts='';
          let paramy = {};
          if (val!=='全部') {
            this.$set(paramy, 'section_two', val);
          }
          this.$axios.post("/api/section-list", paramy).then((res)=> {
//              console.log(res)
              for (var i in res.data.data.data) {
                  dep.push(res.data.data.data[i].section_two);
              }
              this.departs = this.unique(dep);
              this.search(this.currentPage)
          }).catch((error)=> {
              console.log(error);
          });
      },

//    删除人员信息
      deltPer(valId){
	      this.$confirm('此操作将永久删除该人员, 是否继续?', '提示', {
		      confirmButtonText: '确定',
		      cancelButtonText: '取消',
		      type: 'warning'
	      }).then(() => {
		      this.$axios.post('/api/user-del',{
			      "id":valId
		      }).then((res)=> {
//              console.log(res)
			      if(res.data.message==="成功"){
				      this.$message({
					      message: '删除成功',
					      type: 'success',
				      });
				      this.search(this.currentPage);
			      } else if(res.data.message==="该人员参与在某个演练项目中,无法删除"){
				      this.$message.error('该人员正在进行演练，请勿删除');
			      } else{
				      this.$message.error('删除失败');
			      }
		      }).catch((error)=> {
			      console.log(error);
		      });
	      }).catch(() => {
		      this.$message({
			      type: 'info',
			      message: '已取消删除'
		      });
	      });

      },

//      初始化地区部门
      getRegDep() {
          let reg = ["全部"]; //地区暂存
          let dep = ["全部"]; //部门暂存
          this.$axios.post("/api/section-list").then((res)=> {
//              console.log(res)
              for (var i in res.data.data.data) {
                  reg.push(res.data.data.data[i].section_one);
                  dep.push(res.data.data.data[i].section_two);
              }
              this.regions = this.unique(reg);
              this.departs = this.unique(dep);
          }).catch((error)=> {
              console.log(error);
          });
      },

//      去重函数
      unique(array) {
          var obj = {};
          return array.filter(function(item, index, array) {
              return obj.hasOwnProperty(typeof item + JSON.stringify(item))
                  ? false
                  : (obj[typeof item + JSON.stringify(item)] = true);
          });
      },

//      获取人员信息
      getPerson() {
          this.$axios.post("/api/section", {
              number: 10,
              page: 1
          }).then((res)=> {
//              console.log(res);
              this.lists = res.data.data.data;
              this.total = res.data.data.total;
          }).catch((error)=> {
              console.log(error);
          });
      },

//      分页处理
      handleCurrentChange(val) {
          this.currentPage = val;
          this.search(val);
      },

//      添加人员弹框
      show() {
          this.isShow = !this.isShow;
          this.getAdd();
      },

//    隐藏添加人员弹框
      boxHide() {
        //重置表单
          this.resetForm('ruleForm');
          this.isShow = !this.isShow;
      },

//    添加人员信息保存
      submitForm(formName) {
          this.$refs[formName].validate(valid => {
              if (valid) {
                  this.$axios.post('/api/user-add',{
                      "section_id":this.ruleForm.adddept,
                      "name":this.ruleForm.name,
                      "password":this.ruleForm.pass
                  }).then((res)=> {
                    //console.log(res)
                      if(res.data.message==="成功"){
                          this.$message({
                              message: "添加成功",
                              type: "success"
                          });
                          this.isShow = !this.isShow;
                          this.search(this.currentPage);//实时更新数据

                          //重置表单
                          this.resetForm('ruleForm');
                      }
                  }).catch((error)=> {
                        console.log(error);
                  });
              } else {
                  console.log("error submit!!");
                  return false;
              }
          });
      },

      //确定移除xlsx
	  handleRemove(file, fileList) {
//		  console.log(file, fileList);
	  },

      //再次添加文件的提示
	  handleExceed() {
		  this.$message.warning(`当前限制选择 1 个文件`);
	  },

//      重置表单
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
  },

  created() {
      this.getPerson();
      this.getRegDep();
  },
};
</script>

